.feedback-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  gap: 20px;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../../data/images/feedback.jpg") center center/cover no-repeat;
}

.feedback-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 40vw;
  height: 80%;
  max-height: 400px;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 10px;
  overflow-y: auto;
  background-color: white;
}

.feedback-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.feedback-form input,
.feedback-form select,
.feedback-form textarea {
  font-family: 'Inter', sans-serif;
  width: 90%;
  margin: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

@media (max-height: 700px) {
  .feedback-content {
    justify-content: start;
  }
}

@media (max-width: 500px) {
  .feedback-button {
    width: 120px;
    font-size: 14px;
  }
}